export default {
  data() {
    return {
      loading:false,
      formItem:{
        aftersales_bn:'',
        corp_code:'',
        logi_name:'',
        logi_no:''
      },
      show:false,
      selectItem:{
        name:'',
        code:''
      },
      type:'',
      actions:[]
    };
  },
  onLoad:function(options){
    this.formItem.aftersales_bn = options.id
    this.type = options.type
    this.getExpress()
  },
  methods: {
    submit(){
      if(this.formItem.mobile){
        if(this.formItem.mobile.length!==11){
          uni.showToast({
            title: '请输入正确手机号',
            icon: 'none',
          })
          return
        }
      }
      this.$http({
        aurl:'sendback',
        data:this.formItem,
        success:res => {
          if(res.data.code===0){
            uni.showToast({
              title: '填写成功',
              icon: 'none',
            })
            setTimeout(function () {
              uni.navigateBack();
            },500)
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    onCodeSelect(e){
      this.selectItem = e.detail
      this.formItem.corp_code = e.detail.code
      this.formItem.logi_name = e.detail.name
      this.show = false
    },
    onClose(){
      this.show = false
    },
    getExpress(){
      this.$http({
        aurl:'getExpress',
        success:res => {
          if(res.data.code===0){
            for(let i=0;i<res.data.result.length;i++){
              const item = {
                name:res.data.result[i].corp_name,
                code:res.data.result[i].corp_code
             }
             this.actions.push(item)
              this.loading = true
            }
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    }
  },
  mounted() {
  },
};
